






















import { Component, Vue } from 'vue-property-decorator';
import { Divider, Button} from 'vant';
import ScanSign from '@/componets/scan_sign.vue';
import 'vant/lib/divider/style';
import 'vant/lib/button/style';

Vue.use(Divider).use(Button);

@Component({
    components: {ScanSign},
})
export default class originalScanSign extends Vue {

    private signText = '' ;
    public created(): void {
        this.$store.state.title = this.$t('original.apply');
        // 二维码内容
        this.signText = this.$store.state.data.origins[this.$route.params.ind].name + ',' + this.$store.state.data.origins[this.$route.params.ind].addr + ',' + this.$route.params.radio + ',' + this.$route.params.name + ',' + this.$route.params.addr;
    }
    public getSign():void {
        // todo 扫码获取成功签名
        // todo 根据扫码内容修改 $store.sate.data.orglApply
            this.$store.state.data.origins[this.$route.params.ind].status = this.$t('originalStatus.sign');

            this.$router.go(-1);
    }
}
